import React from 'react';
import {List, Toast, Button, Tag, Flex, InputItem} from 'antd-mobile';
import { SubPage } from '../components/SubPage';
import $ from 'axios';
import {withRouter} from 'react-router';
import util from '../util/index';

var data, qzonetoken, g_tk, qq, cookie;
var HOST = 'http://120.78.74.34';
const ls = util.ls;
class QZONE_LIULAN extends React.Component {
  constructor(props) {
    super(props);
    data = JSON.parse(localStorage['login'] || '{}');
    qzonetoken = data.qzonetoken;
    g_tk = data.g_tk;
    qq = data.qq;
    cookie = data.cookie;

    this.state = {
      inputRef: qq || '10001'
    };
  }
  qzoneLiulan() {
    var task = {
      qq: this.state.inputRef.trim(),
      type: 'QZONE_LIULAN'
    };

    if (!util.checkQQ(task.qq)) {
      Toast.info("qq");
      return false;
    }

    var remainTasks = ls.QZONE_LIULAN.getTask();
    if (remainTasks.length) {
      Toast.info('');
      this.startTasks(remainTasks);
      return false;
    }


    var _this = this;
    Toast.loading("", 0);
    $.get(`${HOST}/mzz/index.php?m=Home&c=Huzan&a=getOTask&qq=${task.qq}&type=${task.type}`)
      .then(({data}) => {
        var qqs = data.data;
        if (!qqs.length) {
          Toast.offline('');
          return false;
        }
        if (data.message.indexOf("") > -1) {
          Toast.info("APP");
          return false;
        }

        Toast.info("" + qqs.length + "!");
        _this.setState({qqs: qqs});
        ls.QZONE_LIULAN.addTask(qqs);
        this.startTasks(qqs);
      })
      .catch(err => {
        Toast.offline('');
      });
    }
    startTasks(tasks, idx=0) {
      if (idx >= tasks.length) {
        Toast.info("");
        this.putTask.call(this, {
          type: 'QZONE_LIULAN',
          qqs: tasks,
          qq: this.state.inputRef
        });
        return false;
      }
      var _qq = tasks[idx];
      var d = +new Date();
      Toast.loading(""+idx+"/"+tasks.length+"");
      $.get(`https://h5.qzone.qq.com/mqzone/profile?starttime=${d}&hostuin=${_qq}`)
      .then(({data}) => {
        // 
        this.startTasks(tasks, idx+1);
        ls.QZONE_LIULAN.removeTask(tasks[idx]);
      })
      .catch(e => {
        Toast.offline("");
        this.startTasks(tasks, idx+1);
      });
  }
  putTask(task) {
    Toast.loading("...", 0);
    $.post(`${HOST}/mzz/index.php?m=Home&c=Huzan&a=putTask`, {
        type: task.type,
        qq: task.qq,
        num: task.num,
        report: task.qqs,
        sign: util.sign(task.qq, task.num, task.type)
    }).then(({data}) => {

      Toast.info(data.msg);
      if (data.status) {
        setTimeout(() => {
          this.props.router.push('/result');
        }, 2000);
      }
    }).catch(err => {
      Toast.offline('');
    });
  }
  render() {
    const types = [
      { title: '100', num: 100 },
      { title: '300', num: 300 },
      { title: '500', num: 500 },
      { title: '1000', num: 1000 }
    ];
    const {Item} = List;
    const {Brief} = Item;
    return (
      <SubPage title="">
        <div style={{padding: '10px'}}>
          <Flex>
            <Flex.Item style={{flex: 1}}>
              <img src={'http://q1.qlogo.cn/g?b=qq&nk='+(this.state.inputRef || '10000')+'&s=100'}  style={{ width: '100%'}}/>
            </Flex.Item>
            <Flex.Item style={{flex: 4}}>
                  <InputItem
                    clear
                    placeholder="QQ"
                    value={this.state.inputRef}
                    onChange={
                      val => {
                        this.setState({ inputRef: val});
                      }
                    }></InputItem>
            </Flex.Item>
          </Flex>
          <div>
            
          </div>
          <div>
            <Button style={{backgroundColor: 'rgb(72, 187, 243)', color: '#fff'}} onClick={this.qzoneLiulan.bind(this)}></Button>
          </div>
        </div>
        {/*  */}
        <div>
        <List>
            <Item>
              <Brief></Brief>
              <li style={{marginBottom: '12px', overflow: 'auto', wordSpacing: 'normal', whiteSpace: 'normal', wordBreak: 'break-all', fontSize: '12px'}}>~</li>
              <li style={{marginBottom: '12px', overflow: 'auto', wordSpacing: 'normal', whiteSpace: 'normal', wordBreak: 'break-all', fontSize: '12px'}}>~</li>
            </Item>
          </List>
        </div>
      </SubPage>
    );
  }
  componentWillUnmount() {
    Toast.hide();
  }
}


export default withRouter(QZONE_LIULAN);


