import React from 'react';
import {List, Button, TextareaItem, Flex, InputItem, Toast} from 'antd-mobile';
import { SubPage } from '../components/SubPage';
import $ from 'axios';
import util from '@/util/index.js';

var data, qzonetoken, g_tk, qq, cookie;
const ls = util.ls;
var HOST = 'http://120.78.74.34';

export default class QQ_ZAN extends React.Component {
  constructor(props) {
    super(props);
    data = JSON.parse(localStorage['login'] || '{}');
    qzonetoken = data.qzonetoken;
    g_tk = data.g_tk;
    qq = data.qq;
    cookie = data.cookie;

    this.state = {
      type: {
        title: '100',
        num: 100
      },
      inputRef: qq || '',
      comment: '%%'
    };
  }
  qzoneComment() {
    var remainTasks = ls.QZONE_COMMENT.getTask();
    if (remainTasks.length) {
      Toast.info('');
      this.startTasks(remainTasks);
      return false;
    }

    var task = {
      qq: this.state.inputRef.trim(),
      type: 'QZONE_COMMENT',
      content: this.state.comment
    };

    if (!util.checkQQ(task.qq) || task.content.trim() == '') {
      Toast.info("qq~");
      return false;
    }

    var _this = this;
    Toast.loading("", 0);
    util.Log("Request", `GET ${HOST}/mzz/index.php?m=Home&c=Huzan&a=getUTask&qq=${task.qq}&type=${task.type}`);

    $.get(`${HOST}/mzz/index.php?m=Home&c=Huzan&a=getUTask&qq=${task.qq}&type=${task.type}`)
      .then(({data}) => {
        util.Log("Response", data);
        var qqs = data.data;
        if (!qqs.length) {
          Toast.offline('');
          return false;
        }
        Toast.info("" + qqs.length + "!");
        ls.QZONE_COMMENT.addTask(qqs);
        this.startTasks(qqs);
      })
      .catch(err => {
        Toast.offline('');
      });
  }
  startTasks(tasks, idx=0) {
    if (idx >= tasks.length) {
      Toast.info("");
      this.putTask.call(this, {
        type: 'QZONE_COMMENT',
        qqs: tasks.map(e => e.qq),
        qq: this.state.inputRef,
        content: this.state.comment
      });
      return false;
    }
    var task = tasks[idx];
    var d = +new Date();
    Toast.loading(""+(idx+1)+"/"+tasks.length+"");
    
    util.Log("Request", `POST https://mobile.qzone.qq.com/msgb/fcg_add_msg?qzonetoken=${qzonetoken}&g_tk=${g_tk}`);
    $.post(`https://mobile.qzone.qq.com/msgb/fcg_add_msg?qzonetoken=${qzonetoken}&g_tk=${g_tk}`,{
      res_uin: task.qq,
      format: 'json',
      content: util.renderTpl(task.content || '%%'),
      opr_type: 'add_comment'
    })
    .then(({data}) => {
      util.Log("Respsone", data);
      // 
      if (data.message.indexOf("") > -1) {
        Toast.info("APP");
        return false;
      }
      if (data.message.indexOf("") > -1) {
        Toast.loading("10s...", 0);
        setTimeout(() => {
          this.startTasks(tasks, idx);
        }, 10000);
      } else {
        setTimeout(() => {
          this.startTasks(tasks, idx+1);
        }, 3000);
        ls.QZONE_COMMENT.removeTask(tasks[idx].qq);
      }
    })
    .catch(e => {
      Toast.offline("");
      this.startTasks(tasks, idx+1);
    });
}
putTask(task) {
  Toast.loading("...", 0);
  $.post(`${HOST}/mzz/index.php?m=Home&c=Huzan&a=putTask`, {
      type: task.type,
      qq: task.qq,
      num: task.num,
      content: task.content,
      report: task.qqs,
      sign: util.sign(task.qq, task.num, task.type)
  }).then(({data}) => {

    Toast.info(data.msg);
    if (data.status) {
      setTimeout(() => {
        this.props.router.push('/result');
      }, 2000);
    }
  }).catch(err => {
    console.log(err);
    Toast.offline('');
  });
}
  render() {
    const types = [
      { title: '100', num: 100 },
      { title: '300', num: 300 },
      { title: '500', num: 500 },
      { title: '1000', num: 1000 }
    ];
    const {Item} = List;
    const {Brief} = Item;
    return (
      <SubPage title="">
        <div style={{padding: '10px'}}>
          <Flex>
            <Flex.Item style={{flex: 1}}>
              <img src={'http://q1.qlogo.cn/g?b=qq&nk='+(this.state.inputRef || '10000')+'&s=100'}  style={{ width: '100%'}}/>
            </Flex.Item>
            <Flex.Item style={{flex: 4}}>
                  <InputItem
                    clear
                    placeholder="QQ"
                    value={this.state.inputRef}
                    onChange={
                      val => {
                        this.setState({ inputRef: val});
                      }
                    }></InputItem>
            </Flex.Item>
          </Flex>
          <div>
          <Brief></Brief>
          <TextareaItem
            value={this.state.comment}
            rows={5}
            count={100}
            onChange={
              val => {
                this.setState({ comment: val});
              }
            }
          />
          </div>
          <div>
            <Button style={{backgroundColor: 'rgb(72, 187, 243)', color: '#fff'}} onClick={this.qzoneComment.bind(this)}></Button>
          </div>
        </div>
        {/*  */}
        <div>
          <List>
            <Item>
              <Brief></Brief>
              <li style={{marginBottom: '12px', overflow: 'auto', wordSpacing: 'normal', whiteSpace: 'normal', wordBreak: 'break-all', fontSize: '12px'}}>~~</li>
              <li style={{marginBottom: '12px', overflow: 'auto', wordSpacing: 'normal', whiteSpace: 'normal', wordBreak: 'break-all', fontSize: '12px'}}>
              
              <br/>%%   
              <br/>%%        
              <br/>%%        
              <br/>%%       
              <br/>: %%%%%%,
              <br/>de2018/09/22 12:02:033
              </li>
            </Item>
          </List>
        </div>
    </SubPage>
    );
  }
}


